/**
* Features 
*/
#loadingIndicator {
  display: none;  
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 10001;
}
.loading-bg #loadingIndicator {
  background-color: rgba(255,255,255, .75);
}
body #loadingIndicator .loader {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100px;
  z-index: 1000;
}
body #loadingIndicator .preloader-wrapper {
  position: relative;
  left: -35%;
}
body.loading-bg #loadingIndicator,
body.loading #loadingIndicator {
  display: block;
}

.overlay .loader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  display: none;
}
.overlay .preloader-wrapper {
  position: relative;
  left: -35%;
}
.overlay.loading .loader {
  display: block;
}


