/**
* Overlays 
*/
.overlay {
  position: absolute;
  bottom: 0;
  left:0;
  right: 0;
  height: 0;
  background-color:rgba(255, 255, 255, .95);

  -webkit-transition: all .7s; /* Safari */
  transition: all .7s;
  z-index: 500;
}
.nightMode .overlay {
  background-color:rgba(0, 0, 0, .95);
}
.overlay.maximized {
  height: auto;
  top: 65px;
}
.overlay.error {
  background-color:rgba(50, 0, 0, .95);
  color: #eee;
}
.overlay .controls {
  display: block;
  float: right;
}

.overlay .loader {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  display: none;
  opacity: 0;
  transition: opacity .5s ease-in-out;
  -moz-transition: opacity .5s ease-in-out;
  -webkit-transition: opacity .5s ease-in-out;
}

.overlay .preloader-wrapper {
  position: relative;
  left: -35%;
}

.overlay.loading-bg .loader {
  display: block;
  opacity: 1;
}



/**
* Helpers
